import React, { useEffect, useState } from 'react'
import styled from '@emotion/styled';
import { useMeasure } from "react-use"
import { StaticImage } from 'gatsby-plugin-image';

import Box from '../../../components/Box'
import { responsive } from '../../../components/ThemeProvider/theme';
import LazyLoad from '../../../components/LazyLoad';


const StyledBox = styled(Box)`
  position: absolute;
  display: inline-block;
  perspective: 1000px;
  .card {
    position: relative;
    transition-duration: 0.4s;
    transition-timing-function: ease-in-out;
    transform-style: preserve-3d;
  }
`
const backImg = {
  'custom.cGreen': <StaticImage width={256} height={256} src="c.png" alt="碳中和"/>,
  'custom.zeroGreen': <StaticImage width={256} height={256} src="zero.png" alt="氣候中和"/>,
  'custom.weatherGreen': <StaticImage width={256} height={256} src="weather.png" alt="淨零"/>,
}

const Card = ({
  name,
  src,
  backText,
  frontText,
  width,
  backColor,
  show,
  firstTime,
  setBrowse,
  left,
  top,
  ...props
}) => {
  const [state, setstate] = useState()
  // const [transition, setTransition] = useState()
  // const rotate = useContext(rotateContext)
  // useEffect(() => {
  //   if (state) {
  //     setTimeout(() => setTransition(state), 200)
  //   } else setTimeout(() => setTransition(state), 300)
  // }, [state])
  useEffect(() => {
    if(firstTime && show) {
      setstate(true)
      setTimeout(() => {
        setstate(false)
        setBrowse(false)
      }, 2000);
    }
  }, [show, firstTime])
  const [ref, { width: cardWidth }] = useMeasure()
  return (
    <StyledBox
      ref={ref}
      width={width || responsive('18%', '12.5%')}
      style={{ left, top }}
      cursor="pointer"
      {...props}
    >
      <Box
        className="card"
        transform={state && 'rotateY(180deg)'}
        overflow="visible"
      >
        <Box onClick={() => setstate(!state)}>
          <Box.Relative
            zIndex={3}
            transition="all 0.3s"
            transform="translateZ(1px)"
          >
            <LazyLoad ratio={1}>
              <StaticImage
                src="front.png"
                alt="綠色的圓圈"
              />
            </LazyLoad>
          </Box.Relative>
          <Box.AbsCenter
            // width="60%"
            zIndex={4}
            textAlign="center"
            transform="translate(-50%, -50%) translateZ(1px)"
            fontSize={responsive(`${cardWidth / (width ? 12 : 6.25)}px`, `${cardWidth / (width ? 12 : 7.25)}px`)}
            color="white"
            pointerEvents="none"
          >
            {frontText}
          </Box.AbsCenter>
        </Box>
        <Box
          position="absolute"
          top="0%"
          left="0%"
          width="100%"
          height="100%"
          transform="rotateY(180deg)"
          onClick={() => setstate(!state)}
          fontSize={responsive(`${cardWidth / (width ? 10 : 6.25)}px`, `${cardWidth / 8.5}px`)}
        >
          <Box
            position="absolute"
            width="100%"
            top="50%"
            left="50%"
            transform="translate(-50%, -50%)"
          >
            <LazyLoad ratio={1}>
              {backImg[backColor]}
            </LazyLoad>
          </Box>
          <Box.AbsCenter
            width="75%"
            textAlign="center"
            color={backColor}
          >
            {backText}
          </Box.AbsCenter>
        </Box>
      </Box>
    </StyledBox>
  )
}

export default Card;
