const FocusOne = ({ show }) => {
  const [firstTime, setBrowse] = useState(true)
  return (
    <Flex
      pt={responsive(0, null, '2em')}
      flexDirection={responsive('column', null, 'row')}
      alignItems="center"
      background="custom.lightGreen"
      overflow="hidden"
      position="relative"
    >
      {/* <Box.FullAbs mt="-0.5px" as={Media} greaterThanOrEqual="tablet">
        <LazyLoad ratio={2562 / 244}>
          <StaticImage src="cover_desk_stretch.png" layout="fullWidth" alt="背景接合" />
        </LazyLoad>
      </Box.FullAbs> */}
      <Box width={responsive('100%', '50%' ,'26%')}>
        <Media lessThan="laptop">
          <UpCircle
            img={<StaticImage src="green.png" layout="fullWidth" alt="ESG綠色永續" />}
            // title="關鍵投資焦點"
            text="ESG綠色永續"
            show={show}
            ratio={1092 / 451}
          />
        </Media>
        <Media greaterThanOrEqual="laptop">
          <UpCircle
            img={<StaticImage src="green_desk.png" layout="fullWidth" alt="ESG綠色永續" />}
            // title="焦點1"
            text="ESG綠色永續"
            show={show}
            ratio={516 / 997}
          />
        </Media>
      </Box>
      <Box.Relative pl={responsive('1em', null, 0)} pr={responsive('1em', null, '4em')} width={responsive('100%', null, '74%')}>
        <Box textAlign={responsive('center', null, 'left')}>
          <Text.Bold
            color="custom.green"
            fontSize={responsive('1.125em', '1.5em')}
            letterSpacing={responsive('0.125rem', '0.25rem')}
          >綠色浪潮襲捲全球</Text.Bold>
          <Text.Bold color="custom.darkGray" fontSize={responsive('1em', '1.25em')}>ESG、綠色、永續已成市場主流</Text.Bold>
        </Box>
        <Box pt={responsive('1.5em', '2.5em', '1em')} mx={responsive('-10%', null, 0)} px={responsive(0, null, '10%')}>
          <Box position="relative">
            <LazyLoad ratio={1600 / 950}>
              <StaticImage src="map.png" layout="fullWidth" alt="世界地圖" />
            </LazyLoad>
            {cards.map((d, i) => (
              <Card show={show} setBrowse={setBrowse} firstTime={firstTime} {...d} key={i} />
            ))}
          </Box>
        </Box>
        <Source borderColor="custom.green" mt={responsive('8em', '1em')}>
          <Media greaterThanOrEqual="tablet">
            資料來源：Amundi，凱基投信整理。資料日期：2020/12/31。
          </Media>
          <Media lessThan="tablet">
            資料來源：Amundi，凱基投信整理。<br />資料日期：2020/12/31。
          </Media>
        </Source>
      </Box.Relative>
    </Flex>
  )
}

export default FocusOne
